@media (max-width: 991px) {
    .gh-latest {
        margin-top: 0;
    }
    .gh-pagehead {
        grid-column: main-start/main-end;
        max-width: 480px;
        padding-top: 0;
        position: static;
    }
}
@media (min-width: 992px) {
    .gh-head-inner {
        padding: 0;
        row-gap: 0 !important;
    }
    .gh-head-brand {
        align-items: center;
        display: flex;
        height: 80px;
    }
    .gh-head-menu {
        height: 56px;
    }
    .gh-head-menu:after,
    .gh-head-menu:before {
        background-color: var(--color-light-gray);
        content: "";
        height: 1px;
        left: 0;
        position: absolute;
        top: 80px;
        width: 100%;
    }
    .gh-head-menu:after {
        top: 136px;
    }
}
@supports (-moz-appearance: none) {
    .gh-topic-count {
        padding-bottom: 0.5rem;
    }
    .gh-subscribe-input-btn {
        position: relative;
        top: 1px;
    }
}

